@include telefono {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}

.entrada-blog {
  margin-bottom: 2rem;
  @include telefono {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.texto-entrada {
  a {
    color: $negro;
  }

  h4 {
    margin: 0;

    line-height: 1.4;

    @include telefono {
      margin-top: 0;
    }

    &::after {
      //crear la linea verde a partir de css
      content: "";
      display: block;
      width: 15rem;
      height: 0.5rem;
      background-color: $verde;
      margin-top: 1rem;
    }
  }
}
span {
  color: $amarillo;
}
